import React, { useState } from 'react'
import PropTypes from 'prop-types'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import { AllSiteMetadataQuery } from '@src-queries'

const SiteIndexPage = props => {
	const { location } = props

	const siteMetadata = AllSiteMetadataQuery()

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)

	const pageTitle = siteMetadata.title
	const pageDescription = siteMetadata.description

	const pageValues = {
		title: null,
		description: pageDescription,
		location: location.pathname,
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="simple">
				<p></p>
			</Layout>
		</AppContext.Provider>
	)
}

SiteIndexPage.propTypes = {
	location: PropTypes.object,
}

export default SiteIndexPage
